@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1f1b24;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media(min-width: 820px) {
  .App {
    padding: 20px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
  }
}

@media(max-width: 820px) {
  .App {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
  }
}

.container {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;

  padding: 30px;

  width: 30%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 83vh;

  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;

  font-family: 'Ubuntu', sans-serif;
}

.card-container {
  padding: 5px;


  -ms-overflow-style: none;
  overflow-y: scroll;
  scrollbar-width: none;

  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-gap: 10px;
  gap: 10px;
}

.card-container::-webkit-scrollbar {
  display: none;
}

.container-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.container-top h1 {
  margin: 0;
  color: #c4c4c4;
  font-size: 160%;
}

.card-count {
  margin: auto 0;
  padding: 1px 6px 2px 6px;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  height: 20px;

  border: 1px solid #c4c4c4;
  border-radius: 5px;

  color: #fff;
}

.add-card {
  outline: none;
  border: none;
  border-radius: 10px;

  padding: 10px;

  background-color: rgba(255, 255, 255, 0.1);
  color: #c4c4c4;
  font-family: 'Ubuntu', sans-serif;
}

.add-card:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
}

@media(max-width: 820px) {
  .container {
    width: 85%;
    margin:auto;
  }
}

.rating-container {
  display: flex;
  flex-direction: row;
  grid-gap: 2px;
  gap: 2px;
}

.rating-container p {
  font-size: 1vw;
  color: #c4c4c4;
}

.retro-card-like, .retro-card-dislike {
  border: none;
  outline: none;
  background: none;

  margin: auto 0;
  padding: 3px 6px;

  display: flex;

  color: #e7e7e7;
}

.retro-card-like:hover, .retro-card-dislike:hover {
  cursor: pointer;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

.retro-card-dislike > img, .retro-card-like > img {
  width: 1.2vw;
}

.retro-card-dislike > img {
  margin-top: 2px;
}

@media(max-width: 820px) {
  .rating-container p {
    font-size: 0.8rem;
  }

  .retro-card-dislike > img, .retro-card-like > img {
    width: 15px;
  }
}

.retroCard {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 90%;

  padding: 5%;
  padding-bottom: 0;

  border: 2px solid;
  border-radius: 10px;

  color: #fff;
}

.retro-card-content {
  word-break: break-word;
}

.retro-card-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  bottom: 0;
}

.retro-card-date {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  grid-gap: 15px;
  gap: 15px;

  font-size: 0.9vw;

  bottom: 0;
  margin: auto 0;

  color: #c4c4c4;
}

.retro-card-btns {
  display: flex;
  grid-gap: 0;
  gap: 0;
  bottom: 0;
}

.retro-card-delete {
  border: none;
  outline: none;
  background: none;

  margin: auto 0;
  padding: 3px 6px;

  display: flex;

  color: #e7e7e7;
}

.retro-card-delete:hover {
  cursor: pointer;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

.retro-card-delete > img {
  width: 1.2vw;
}

@media(max-width: 820px) {
  .retro-card-date {
    font-size: 0.7rem;
  }

  .retro-card-btns p {
    font-size: 0.8rem;
  }

  .retro-card-delete > img {
    width: 15px;
  }
}

.newCard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  width: 90%;

  padding: 5%;

  border: 2px solid #e7e7e7;
  border-radius: 10px;

  color: #e7e7e7;
  font-family: 'Ubuntu', sans-serif;
}

.new-card-input {
  outline: none;
  border: none;
  background: none;

  color: #e7e7e7;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1rem;
}

.new-card-btn {
  outline: none;
  border: none;
  background: none;

  height: 25px;
  width: 25px;

  border: 2px solid #e7e7e7;
  border-radius: 50%;

  color: #e7e7e7;
  font-size: 1rem;
}

.new-card-btn:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
}

